import React, { useState, useEffect } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

function Detail(props) {
useEffect(()=>{
    setTimeout(()=>{
        alertEdit(false);
    }, 2000)
});

let [alert, alertEdit] = useState(true);
let history = useHistory();

let { id } = useParams();
let searchedProduct = props.shoes.find(product => { 
    return product.id === parseInt(id) 
});

let 박스 = styled.div`
    padding : 20px;
`;
let 제목 = styled.h4`
    font-size : 25px;
`;

    return (
        <div className="container">
            <박스>
                <제목>Detail</제목>
            </박스>

            {
                alert === true
                ? <div className="my-alert">
                    <p>재고가 얼마 남지 않았습니다</p>
                </div>
                : null
            }
            
            <div className="row">
                <div className="col-md-6">
                    <img src={`https://codingapple1.github.io/shop/shoes${(searchedProduct.id)+1}.jpg`} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{searchedProduct.title}</h4>
                    <p>{searchedProduct.content}</p>
                    <p>{searchedProduct.price}원</p>
                    <Info 재고={props.재고}></Info>
                    <button className="btn btn-danger" onClick={()=>{ props.재고변경([9,11,12]) }}>주문하기</button> 
                    <button className="btn btn-danger" onClick={()=>{ history.goBack() }}>뒤로가기</button>
                </div>
            </div>
        </div>
    )
}

function Info(props) {
    return (
        <p>재고: {props.재고}</p>    
    )
}

export default Detail